import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { HomeComponent } from '../home/home.component';
import { AboutComponent } from '../about/about.component';
import { AboutidComponent } from '../about/aboutid/aboutid.component';
import { MobilesComponent } from '../about/aboutid/mobiles/mobiles.component';
import { LaptopsComponent } from '../about/aboutid/laptops/laptops.component';
import { SoftweresComponent } from '../about/aboutid/softweres/softweres.component';
import { SamplepostComponent } from '../samplepost/samplepost.component';
import { ContactComponent } from '../contact/contact.component';
import { HomedetailComponent } from '../home//homedetail/homedetail.component';
import { Room1Component } from '../home/homedetail/room1/room1.component';
import { Room2Component } from '../home/homedetail/room2/room2.component';
import { SignupComponent } from '../signup/signup.component';
import { SigninComponent } from '../signin/signin.component';
import { InOutputComponent } from '../in-output/in-output.component';



const routes: Routes = [
  {path:'home',component:HomeComponent},
  {path:'about',component:AboutComponent},
  {path:'aboutid',component:AboutidComponent,
    children:[
          {path:'mobiles',component:MobilesComponent},
          {path:'laptops',component:LaptopsComponent},
          {path:'softweres',component:SoftweresComponent}
    ]

},
  {path:'samplepost',component:SamplepostComponent},
  {path:'contact',component:ContactComponent},
  {path:'in-output',component:InOutputComponent},
  {path: '', component: HomeComponent},
  {path:'signup', component:SignupComponent,outlet:'customRouter'},
  {path:'signin', component:SigninComponent,outlet:'customRouter'},

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class SpaRoutingModule { }





