import { Component, OnInit,  EventEmitter, Input, Output} from '@angular/core';
import {Records} from '../records';

@Component({
  selector: 'app-inner-outerput',
  templateUrl: './inner-outerput.component.html',
  styleUrls: ['./inner-outerput.component.css']
})
export class InnerOuterputComponent implements OnInit {

  edit=true;
  desc='';
  data:any;
  @Input() moreData: Records;
  @Output() addDesc = new EventEmitter<any>();
  ngOnInit() {
  }
  updated(moreData,desc){
    this.desc='';
    this.data={"state":moreData,"desc":desc}
    this.addDesc.emit(this.data);

  }

}



