import { Component, OnInit } from '@angular/core';
import {Http,Response} from '@angular/http';
import {Observable} from 'rxjs';
import {DatabaseService} from '../database.service';
import {Employee} from '../employee';

@Component({
  selector: 'app-samplepost',
  templateUrl: './samplepost.component.html',
  styleUrls: ['./samplepost.component.css']
})
export class SamplepostComponent implements OnInit {
   
  listObservable:Observable<Employee[]>;
  listData:Employee[];
  errorMessage:string;



  constructor( private databaseService:DatabaseService ) { }

  ngOnInit(): void {
    this.listObservable=this.databaseService.getEmployeeData();
    this.listObservable.subscribe(
      listData=>this.listData=listData,
      error=>this.errorMessage=<any>error);
  }
 postdata()
 {
   this.databaseService.postObsarvable().subscribe(
   ()=>{console.log("success")},
   error=>{console.log("something is wrong")}
   );
 }
 deleteData()
 {
   this.databaseService.delObsarvable().subscribe(
     ()=>{console.log("successfully deleted")},
    error=>{console.log("something is wrong")}
   );
 }
}



