import { Injectable } from '@angular/core';
import {Http, Response} from '@angular/http';
import {Observable} from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

import {Employee} from './employee';

@Injectable()
export class DatabaseService {
        url='http://localhost:3000/posts/';
        delUrl='http://localhost:3000/posts/8';

        values:any={
            name:"santhu",
            email:"santhu@gmail.com",
            mobile:898798789
        };
  constructor(private http:Http) { }
 getEmployeeData():Observable<Employee[]>
 {
           return this.http.get(this.url)
           .map( this.employeeData)
           .catch(this.handleErrorObservable);
 }

 private employeeData(resp: Response)
 {
    let data=resp.json();
    return data;
 }
 private handleErrorObservable(error: Response | any)
 {
     return Observable.throw(error.message || error);
 }
postObsarvable()
{
    return this.http.post(this.url,this.values);
}
delObsarvable()
    {
        return this.http.delete(this.delUrl);
    }


}



